
import { defineComponent, ref } from "vue";

export default defineComponent({
  emits: ["change-option"],
  setup() {
    const currentYear = new Date().getFullYear();
    const lastFourYears = Array.from(
      { length: 4 },
      (_, i) => currentYear - i
    ).map((year) => year.toString());
    const selectedOption = ref("all");

    return {
      lastFourYears,
      selectedOption,
    };
  },
});
